import { User } from "./auth";

export interface Post {
    _id: string;
    text: string;
    user: User;
    name: string;
    avatar: string;
    likes: Like[];
    comments: Comment[];
}

export interface PostState {
    post:Post | null;
    posts: Post[];
    isLoading: boolean;
    error: string | null;
}

export const initialPostState: PostState = {
    post: null,
    posts: [],
    isLoading: false,
    error: null,
};
export interface Like {
    userId: string;
    postId: string;
}
export interface Comment {
    _id: string;
    userId: string;
    text: string;
}