/** @format */
"use client";
import { useCallback } from 'react';
import { useAppDispatch, useAppSelector } from '../store/hooks';
import { createPost } from '../store/slices/postSlice';
import { initialPostState, Post } from '../store/types/post';
import { addAlert } from '../store/slices/alertSlice';
import { getErrorMessage } from '@/errorHandler';

export const usePost = () => {
	const dispatch = useAppDispatch();
	const postState = useAppSelector((state) => state.posts);

	const savePost = useCallback(
		async (postData: Post) => {
			try {
				// Dispatch action to save post
				const result = await dispatch(createPost(postData)).unwrap();
				dispatch(
					addAlert({
						id: `${Date.now()}`,
						type: 'success',
						message: 'Post created successfully!',
						duration: 5000,
					})
				);
				return result;
			} catch (error) {
				// Handle error
				dispatch(
					addAlert({
						id: `${Date.now()}`,
						type: 'error',
						message: getErrorMessage(error) || 'Profile Error',
						duration: 5000,
					})
				);
			}
		},
		[dispatch]
	);
	return { 
        createPost:savePost,
        post:postState.post,
        posts:initialPostState.posts,
        isLoading:postState.isLoading,
        error:postState.error
    };
};
